@font-face {
  font-family: "digitalix";
  src: local("digitalix"), url("../fonts/digitalix.ttf") format("truetype");
  font-weight: regular;
}
@font-face {
  font-family: "Minecraft";
  src: local("Minecraft"), url("../fonts/Minecraft.ttf") format("truetype");
  font-weight: regular;
}
@font-face {
  font-family: "nokiafc22";
  src: local("nokiafc22"), url("../fonts/nokiafc22.ttf") format("truetype");
  font-weight: regular;
}

.App {
  overflow-y: hidden;
  margin:0;
  padding:0;
  display: flex;
  align-items: stretch;
  height: 100vh;
  #home{
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    flex-direction: column;
    width:100vw;
    margin:0;
    padding: 20px 0;
    background-color: #ffffff;
    font-family: "digitalix";
    color: #008ce9;
    #main_home{
      display: flex;
      justify-content: start;
      flex-direction: column;
      font-size: 1.5em;
      align-items: center;
      text-align: center;
      flex-grow: 1;
      div{
        display: flex;
        flex-grow: 3;
        #photo_me{
          display: flex;
          flex-grow: 3;
          transform: translateY(50px);
        }
        #footer{
          display: flex;
          flex-grow: 1;
          justify-content: center;
          align-items: center;
          flex-direction: row;
          font-size: 0.75em;
          #logo_git{
            width: 40px;
            height: 40px;
            margin-right: 10px;
          }
          a{
            text-decoration: none;
            color: initial;
          }
        }
      }
    }
  }
}
.Console{
  border-radius: 10px;
  margin: auto 4em;
  padding: 10px;
  box-shadow: rgba(0,0,0,0.5) 0px 0px 25px;
  background-color: black;
  height: 80vh;
  width:100vw;
  font-size: 13px;
  overflow-y: scroll;
  pre{
    margin:0;

  }
  .preCommand{
    margin:0;
    display: flex;
    flex-direction: row;
    .nameCommand{
      color: #008ce9;
      margin:0;
    }
  }
  code{
    display: flex;
    text-align: left;
    margin-bottom: 0.2em;
    margin-left: 1em;
  }
  #cursor{
    width: 10px;
    height: 15px;
    background-color: white;
    margin-top: 2px;
    margin-left: 4px;

  }
}

@media (prefers-reduced-motion: no-preference) {
  #cursor {
    animation: cursor_animation infinite 0.5s cubic-bezier(1, 0, 0, 1) alternate;
  }
  #photo_me{
    //animation: blur_animation 1 10s cubic-bezier(0.0, 0.1, 0.5, 1.0);
  }
}

@keyframes cursor_animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes blur_animation {
  from {
    filter: blur(10px);
  }
  to{
    filter: blur(0);
  }
}

